import d from './data.json';
import jpgImages from '../images/technology/*.jpg';



function main() {

    const nav = document.querySelectorAll('.technology__nav__link');
    const subtitle__number = document.querySelector('.technology__subtitle--number');
    const name = document.querySelector('.technology__title--name');
    const terminology = document.querySelector('.technology__title--terminology')
    const text = document.getElementById('technology__text');
    const picture = document.getElementById('technology__image');
    const image = document.querySelector('.technology__image');
    let data = d.technology;

    const technology = {
        '01': 0,
        '02': 1,
        '03': 2,
    }

    nav.forEach(elm => {
        elm.addEventListener('click', handelNav);
    })


    function handelNav(e) {
        nav.forEach(elm => {
            elm.classList.remove('active');
        });
        image.classList.add('zoomIn');
        terminology.classList.add('fadeIn');
        name.classList.add('fadeIn');
        text.classList.add('fadeIn');
        setTimeout(() => {
            image.classList.remove('zoomIn');
            terminology.classList.remove('fadeIn');
            name.classList.remove('fadeIn');
            text.classList.remove('fadeIn');
        }, 500)

        e.target.classList.add('active');
        let number = e.target.dataset.id;
        const current = technology[number];
        subtitle__number.textContent = number;
        name.textContent = data[current].name;
        text.textContent = data[current].description;
        handleImage('/', data[current].images.landscape, data[current].images.portrait, picture);

    }

    function handleImage(path, jpgLandscape, jpgPortrait, picture) {
        picture.children[0].srcset = jpgImages[jpgLandscape];
        picture.children[1].srcset = jpgImages[jpgPortrait];
        picture.children[2].src = path + jpgPortrait;
    }


}

window.addEventListener('DOMContentLoaded', main);